// mixin用名前空間の宣言
 @use '../../foundation/mixin' as mixin;



// Work、about、skill、 contactの共通
.p-article {
    background-color: gold;
    width: 100vw;
    height: 100%;

    &__contents {
        width: 100vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__header {
        width: 920px;
        display: flex;
        justify-content: space-between;
    }
    &__header-l {
        display: flex;
        position: relative;

        >h2 {
            position: absolute;
            left: 85px;
            top: -10px;
            font-size: 96px;
            
        }
    }
}

// ファーストビュー
.p-firstview  {
    width: 100vw;
    height: 700px;
    background: url(/img/FV_shikak.png) no-repeat left bottom /80px 80px,
                url(/img/firstview.jpg) center/cover;
    @include mixin.verticalcenter;
    justify-content: center;
        &__title {
          width: 920px;
          font-size: 104px;
        }
}


// Work、about、skill、 contactの共通部分
.p-topwork {
    position: relative; //Gridと、Moreリンクの位置基準    
    height: 1200px;
    width: 920px;
        &__header {
          display: flex;
          justify-content: space-between;
          
        
        }
        &__header-l {
          height: 600px;
          width: 320px;
          background-color: black;
          display: flex;

              >p {
              font-family: 'Slabo 27px', serif;
              font-size: 72px;
              position: relative;
              }
          
              >p::after {
              @include mixin.whiteunderline;
              }
        }
        &__header-r{
          display: flex;
          flex-direction: column;
          align-items: flex-end;

              >h2 {
                font-family: 'Slabo 27px', serif;
                font-size: 112px;
                height: 101px;
              }
        }
        
        &__body {
          position: absolute;
          top: 250px;
          right: 0px;
        
        }


        &__grid {
          display: grid;
          grid-template-columns: repeat(2, 400px);
          grid-template-rows: repeat(2,400px);;
          
          >dl {
            margin-left: 40px;
            text-align: center;
            
          }
        }

        &__footer {
          @include mixin.topfooter(1100px);

        }

        &__linkbox {
          height: 40px;
          width: 200px;
          display: flex;
          align-items: center;

          >a {
            display: block;
          
          }          
        }

} 

// Aboutセクション
.p-topabout {
    position: relative;  //紹介文とMoreリンクの位置基準
    background-color: red;
    height: 700px;
    width: 920px;
        &__body {
            height: 350px;
            position: absolute;
            top: 250px;
            left: 160px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }

        &__name {
            display: flex;
            >p {
              margin-left: 30px;
            }
        }

        &__footer {
            @include mixin.topfooter(600px);
        }

}

// skillセクション
.p-topskill {
    position: relative; //紹介文とMoreリンクの位置基準
    height: 1000px;
    width: 920px;
    background-color: rebeccapurple;
        &__body {
            position: absolute;
            top: 250px;
            left: 160px;
            height: 700px;
            background-color: saddlebrown;
            @include mixin.verticalspace-between
          
        }
        &__skill {
            height: 630px;
            @include mixin.verticalspace-between
        }

        &__skillbox {
            height: 170px;
            background: salmon;
            @include mixin.verticalspace-between

        }

        &__footer {
            position: absolute;
            top: 910px;
        }
}

// Contactセクション
.p-topcontact {
    position: relative; //説明文とFormの位置基準
    height: 1000px;
    width: 920px;
    background-color:black;
        &__body {
          position: absolute;
          top: 250px;
          left: 160px;
          height: 600px;
          
        }

        &__text {
          height: 100px;
          @include mixin.verticalspace-between;
        }

        &__form {
          margin-top: 40px;
          height: 480px;
          @include mixin.verticalspace-between;
        }
        &__content {
          display: flex;

          >label {
              width: 160px;
              height: 30px;
              
          }

          >input {
              width: 500px;
              height: 30px;
          }

          >textarea {
              width: 500px;
              height: 240px;
          }
        }

        &__footer {
          @include mixin.topfooter(900px);
        }
}

// Sideバー
.p-topside  {
    &__btn {
      position: absolute;
      top: 50px;
      right: 100px;
    }

    &__link {
      position: absolute;
      top: 300px;
      list-style: none;
      height: 400px;
      background-color: cadetblue;
    }

    &__chara {
        font-size: 24px;
        color: black;
        font-family: 'Slabo 27px', serif;
       
    }
}

//footer 
.p-footer {
     height: 80px;
     @include mixin.verticalspace-between;
     align-items: center;
     margin-top: 40px;

     &__link {
         width: 920px;
         display: flex;
         justify-content: space-between;
     }
}

// Workページのコンテンツ
.p-workcontent {
     width: 920px;
     &__header {
        display: flex;
        justify-content: space-between;
     }
     &__header-l {
        @include mixin.verticalcenter;
          >p {
              margin-left: 20px;
          }
     }
}